import moment from 'moment'
export default {
  data: () => ({
    rate: null,
    pictureUrl: null,
    headers: [
      {
        text: 'เลขทะเบียน',
        sortable: false,
        align: 'start',
        value: '_id',
      },
      {
        text: 'วันยืม',
        value: 'start_date',
        sortable: false,
      },
      {
        text: 'กำหนดส่ง',
        value: 'end_date',
        sortable: false,
      },
    ],
  }),
  computed: {
    user() {
      return this.$store.getters['me/GET_USER']
    },

    transactions() {
      return this.$store.state.transaction.transactions.map((transaction) => {
        transaction.start_date = moment(transaction.start_date).format(
          'YYYY-MM-DD'
        )
        transaction.end_date = moment(transaction.end_date).format('YYYY-MM-DD')

        return transaction
      })
    },

    borrowed() {
      console.log(this.$store.state.me.borrowed)
      return this.$store.state.me.borrowed
    },

    remain() {
      console.log(this.$store.state.me.remain)
      return this.$store.state.me.remain
    },
  },

  async created() {
    const job = this.$store.state.me.job
    console.log('JOB', job)
    const rate = await this.$store.dispatch('transaction/rate', job)
    this.rate = rate

    this.pictureUrl = this.$store.getters['me/GET_PICTURE_URL']
    await this.$store.dispatch('transaction/fetchHistoryTransactions')
    await this.$store.dispatch('me/fetchBorrowCount')
  },
}
